import React from 'react';
import ImageGallery from 'react-image-gallery';

import './home.css';

const Home = () => {
  
  const images = [
    {
      original: 'images/gallery/konark.jpeg',
      description: 'description',
      originalAlt: 'description'
    },
    {
      original: 'images/gallery/Yoga for Women.png',
      description: 'Yoga for Women',
      originalAlt: 'Yoga for Women'
    },
    {
      original: 'images/gallery/split.jpg',
      description: 'description',
      originalAlt: 'description'
    },
    {
      original: 'images/gallery/konark2.jpeg',
      description: 'description',
      originalAlt: 'description'
    },
    {
      original: 'images/gallery/Chhavi from Canada in a classical Hatha Yoga pose.png',
      description: 'Classical Hatha Yoga pose',
      originalAlt: 'Chhavi from Canada in a classical Hatha Yoga pose'
    }
  ];
  
  return (
      <section id="home">
        <ImageGallery items={images} showPlayButton={false} autoPlay={true} />
      </section>
  )
}

export default Home