import React from 'react'
import { Grid, Typography, Button } from '@mui/material'

import hero from '../../assets/hero.png';
import tl1 from '../../assets/about/timeline_1.png';
import tl2 from '../../assets/about/timeline_2.png';
import tl3 from '../../assets/about/timeline_3.png';

import './about.css'


const About = () => {
  return (
    <section id="about">
      <div class="about-info">
        <Typography variant="h3" className="about-title">
          Welcome!
        </Typography>
        <Typography className="about-text">
            <b>YogaMitali</b> is a private Yoga Centre which was formed in 2019 and which runs around the Globe. It is basically an Online Platform providing general Yoga classes in Groups, Individual, Therapy Classes to almost all age groups.
        </Typography>
        <Typography className="about-text">
            The owner and the Instructor of YogaMitali is Mrs. <b>Maitreyee Sharma</b>, a Certified Yoga Instructor from a renowned  Institute S-Vyasa, Bengaluru. She is certified Pre and Post Natal Yoga Instructor. She is presently in her final semester of M.Sc. Yoga from Tamil Nadu Physical Education and Sports University. She is also a certified Yoga Therapist under the Indian Yoga Association and Asian Yoga Therapy Association.
        </Typography>
        <Typography className="about-text">
          Since the past 3 years, YogaMitali has been a friend to innumerable suffering Souls. Maitreyee has worked with people suffering from different variety of ailments - Alzheimers, Foot Drop, Infertility issues, PCOD, Gastro Intestinal problems etc. Her major area of work is with <b>Women</b> and <b>Elderly</b> population.
        </Typography>
        <Typography className="about-text">
          Countries where YogaMitali caters to are New Zealand, Australia, Canada and India. She tries her best to follow the <b>Classical School of Yoga</b>. In the present world Yoga has entered into the Glamour Industry. Maitreyee continuously fights against this misappropriation of Yoga and shall continue working towards this aim.
        </Typography>
      </div>

      <Grid container className="instructor-grid">
        <Grid xs={12} md={6} className="hero-img">
          <img src={hero} alt="From the Instructor's mouth" />
        </Grid>
        <Grid xs={12} md={6} className="hero-caption">
          <Typography variant="h3" className="title">
            From the Instructor's Mouth
          </Typography>
          <Typography className="text">
            My acquaintance with Yoga started when I was in complete misaligned state of body and mind. I was diagnosed with hormonal issues, gastrointestinal problems, acute back pain and migraine. I had gone through many alternative therapies and finally resorted to Yoga. After coming to Yoga, what I realised is my Body started listening to the Mind and my Breath. This is what Yoga is all about  <i>Yujyate anena iti Yogah</i> (Yoga is that which joins).
          </Typography>
        </Grid>
      </Grid>

      <Grid container className="philosophy-grid">
        <Grid xs={12} style={{ margin: "auto" }}>
          <Typography variant="h3" className="title">
            My Philosophy
          </Typography>
          <Typography className="text">
            Like me there are thousands who are disconnected from their breath and mind. My idea is to help individuals to get connected to their origin and go beyond their pains. For me Yoga is not about doing Asanas, but being in Asanas. I generally follow the Patanjala School of Yoga but believe in Swami Vivekanandas Four Schools Of Yoga  Raja, Jnana, Bhakti and Karma Yoga because all these schools lead to the same path  attaining bliss in physical, social and psychological planes.
          </Typography>
        </Grid>
        <Grid xs={6} md={3} style={{padding: "0.5rem"}}>
          <Button className="philosophy-btn">
            <img src={tl1} alt="Raja Yoga"/>
            <Typography variant="h5" className="caption">
              Raja Yoga
            </Typography>
          </Button>
        </Grid>
        <Grid xs={6} md={3} style={{padding: "0.5rem"}}>
          <Button className="philosophy-btn">
            <img src={tl2} alt="Jnana Yoga"/>
            <Typography variant="h5" className="caption">
              Jnana Yoga
            </Typography>
          </Button>
        </Grid>
        <Grid xs={6} md={3} style={{padding: "0.5rem"}}>
          <Button className="philosophy-btn">
            <img src={tl3} alt="Bhakti Yoga"/>
            <Typography variant="h5" className="caption">
              Bhakti Yoga
            </Typography>
          </Button>
        </Grid>
        <Grid xs={6} md={3} style={{padding: "0.5rem"}}>
          <Button className="philosophy-btn">
            <img src={tl3} alt="Karma Yoga"/>
            <Typography variant="h5" className="caption">
              Karma Yoga
            </Typography>
          </Button>
        </Grid>
      </Grid>
    </section>
  )
}

export default About