import React from 'react'
import { Typography, Card, CardContent, CardMedia } from '@mui/material'
import Carousel from 'react-material-ui-carousel'


import './testimonials.css'

function Item(props)
{
    return (
        <Card className="testimonial">
          <CardContent>
            {props.item.video ? <CardMedia component="video" controls src={props.item.video} style={{ paddingBottom: "2rem" }} /> : ""}
            {props.item.comments ? <Typography variant="h5" className="comments">{props.item.comments}</Typography> : ""}
            <div style={{ display: "flex", alignItems: "center", justifyContent: "center", gap: "1rem" }}>
              {props.item.img ? <img src={props.item.img} /> : ""}
              <Typography variant="h6" className="user-name">{props.item.user}</Typography>
            </div>
          </CardContent>
        </Card>
    )
}

const items = [
  {
    user: "Chhavi, USA",
    comments: "I wanted yoga to help me in connecting with myself, to be more present in reality. Now when I look back before I joined you, I see so much changes in me now. Yes the body has changed tremendously, I feel so balanced and calm within my whole body. My body has become so light. But with all that, my mind, it has completely shifted.",
    img: "",
    video: ""
  },
  {
    user: "Ashley, Toronto",
    comments: "I started Maitreyees morning yoga class after struggling for almost a year with pain in my back, neck, shoulder, arm, all the way to my wrist. Through morning meditation and yoga, I  learned how to relax my body and mind. Learning to be present and my connection to my breath helped me navigate my world physically with more ease.",
    img: "images/ashley.jpeg",
    video: ""
  },
  {
    user: "Sindrajeet, Toronto",
    comments: "",
    img: "",
    video: "video/foot drop.mp4"
  },
  {
    user: "Keireen, Toronto",
    comments: "",
    img: "",
    video: "video/keireen canada.mp4"
  }
]

const Testimonials = () => {
  return (
    <section id="testimonials">
      <Typography variant="h3" className="title">
        Client Reviews
      </Typography>
      <Carousel indicators={false} className="testimonials-carousel">
        {
          items.map( (item, i) => <Item key={i} item={item} /> )
        }
      </Carousel>
    </section>
  )
}

export default Testimonials